  top: 0;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  visibility: hidden;
  opacity: 0;
  box-shadow: 0px 0px 10px 2px rgba(28, 28, 28, 0.25) inset;
  -webkit-transform: translateX(00px);
          transform: translateX(00px);
  transition: -webkit-transform 2s 1s 0s;
  transition: transform 2s 1s 0s;
  transition: transform 2s 1s 0s, -webkit-transform 2s 1s 0s;
  box-sizing: border-box;
}

