.data__menu-btn {
  border-radius: 50%;
  background-color: rgb(237, 237, 237, 0.2);
  border: 1px solid gainsboro;
  width: 50px;
  height: 50px;
  margin: 10px auto;

  box-shadow: -1px -2px 1px 1px rgb(139, 139, 139, 0.7) inset;

  color: transparent;
  font-size: 14px;
  overflow: hidden;
  transition: width 0.5s ease, border-radius 2.5s, box-shadow 0.5s ease,
    color 0.1s 0s linear;
}

.data__menu-btn:hover {
  background-color: #dadada;
}

@media screen and (max-width: 1100px) {
  .data__menu-btn {
    width: 40px;
    height: 40px;
    max-width: 120px;
    font-size: 8px;
  }
}

@media screen and (max-width: 600px) {
  .data__menu-btn {
    margin: 10px 10px 10px 20px;
    width: 40px;
    height: 40px;
  }
}

.data__menu-btn_container {
  display: flex;
  position: absolute;
  flex-direction: column;
  max-width: 180px;
  left: 15px;
  top: 180px;
  transition: width 0.3s 0s linear;
}

.data__menu-btn_container:hover > .data__menu-btn {
  width: 140px;
  border-radius: 25px;
  right: 0;
  font-size: 15px;
  overflow: visible;
  color: rgb(40, 40, 40);
  transition: width 0.3s 0s ease-in, color 0.3s 0.15s ease,
    background-color 1s 1.5s ease;
}

.data__menu-btn_container:hover + .main__content {
  left: 150px;
  -webkit-transform: translateX(60px);
          transform: translateX(60px);
  transition: left 0.3s 0s ease, -webkit-transform 0.25s 0s linear;
  transition: left 0.3s 0s ease, transform 0.25s 0s linear;
  transition: left 0.3s 0s ease, transform 0.25s 0s linear, -webkit-transform 0.25s 0s linear;
}
.data__menu-btn_container + .main__content {
  left: 70px;
  transition: left 0.4s 0.15s ease;
}

.data__menu-btn_container:hover
  + .main__content
  > .cover__content
  > .cover__label {
  opacity: 0;
  transition: opacity 0.2s 0s ease;
}
.data__menu-btn_container + .main__content > .cover__content > .cover__label {
  opacity: 1;
  transition: opacity 0.3s 0.45s ease;
}

@media screen and (max-width: 1100px) {
  .data__menu-btn_container {
    max-width: 90px;
    left: 10px;
  }
  .data__menu-btn_container:hover > .data__menu-btn {
    width: 120px;
    height: 35px;
    font-size: 12px;
  }
  .data__menu-btn_container:hover + .main__content {
    left: 90px;
  }
}
@media screen and (max-width: 600px) {
  .data__menu-btn_container {
    display: none;
  }
  .data__menu-btn_container:hover + .main__content {
    display: none;
  }
  .data__menu-btn_container + .main__content {
    left: 0px;
    transition: left 0.4s 0.15s ease;
  }
}

